export default "import React from 'react';\nimport ChoroplethSvg from '@splunk/dashboard-visualizations/ChoroplethSvg';\nimport { campusSvg } from '../../mockdata/campusSvg.js';\n\nconst CampusData = {\n    fields: [\n        {\n            name: 'buildings',\n        },\n        {\n            name: 'values',\n        },\n    ],\n    columns: [\n        ['ATX-270-B', 'N-224', 'VM-203', 'RF-380'],\n        [10000, 55000, 90000, 75000],\n    ],\n};\n\nconst gradientEncoding = {\n    featureId: 'primary[0]',\n    value: 'primary[1]',\n    fill: {\n        field: 'primary[1]',\n        format: {\n            type: 'gradient',\n            values: ['#FF7152', '#4BEBA8'],\n        },\n    },\n};\n\nconst baseProps = {\n    loading: false,\n    width: 700,\n    height: 500,\n    options: {\n        svg: campusSvg,\n    },\n    dataSources: {\n        primary: { data: CampusData },\n    },\n};\n\nexport default () => <ChoroplethSvg {...baseProps} encoding={gradientEncoding} />;\n";