export default "import React from 'react';\nimport ChoroplethSvg from '@splunk/dashboard-visualizations/ChoroplethSvg';\nimport { campusSvgDataURI } from '../../mockdata/campusSvgDataURI.js';\n\nconst CampusData = {\n    fields: [\n        {\n            name: 'buildings',\n        },\n        {\n            name: 'values',\n        },\n    ],\n    columns: [\n        ['ATX-270-B', 'N-224', 'VM-203', 'RF-380'],\n        [10000, 55000, 90000, 75000],\n    ],\n};\n\nconst rangevalueEncoding = {\n    featureId: 'primary[0]',\n    value: 'primary[1]',\n    fill: {\n        field: 'primary[1]',\n        format: {\n            type: 'rangevalue',\n            ranges: [\n                { from: 80000, value: '#4BEBA8' },\n                { from: 60000, to: 80000, value: '#F4DF7A' },\n                { from: 20000, to: 60000, value: '#FC9850' },\n                { to: 20000, value: '#FF7152' },\n            ],\n        },\n    },\n};\n\nconst baseProps = {\n    width: 700,\n    height: 500,\n    options: {\n        svg: campusSvgDataURI,\n    },\n    dataSources: {\n        primary: { data: CampusData },\n    },\n};\n\nexport default () => <ChoroplethSvg {...baseProps} encoding={rangevalueEncoding} />;\n";